import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);


function concat_obj(target, source) { // 合并对象
  for (var i in source) {
    target[i] = source[i];
  }
  return target;
}


export default new Vuex.Store({
  state:{
    jas_test:100,
    medicalArchival:{ // 病历档案表
      id: '', // 主键id
      patient_id: '', // 病人id（外键）
      surgery_time: '', // 手术时间
      startTime: '', // 初诊时间
      explain: '', // 病情说明
      activity: '', // 活动不足
      process: '', // 病程
      department: '', // 科室
      hospital: '', // 手术医院
      doctorName: '', // 主刀医生
      describe: '', // 病历描述
      createtime: '', // 创建时间
      updatetime: '' // 更新时间
    },
    user:{ // 用户表
      id: '', // 主键id
      openid: '', // 用户标识
      realname: '', // 真实姓名 // 李会长
      sex:2, // 性别
      phone: '', // 手机号码 // 18817352333
      birth: '', // 出生年月
      scoreValue: '', // 积分分值
      headPortrait: '', // 用户头像
      createtime: '', // 创建时间
      updatetime: '', // 更新时间
    },
    doctor:{ // 治疗师表
      id: '', // 主键id
      userId: '', // 用户id（外键）
      hospital: '', // 医院
      professiona: '', // 职称
      description: '', // 简介
      department: '', // 科室
    }
  },
  mutations: {
    num_add (state,num2) {
      state.jas_test+=num2
    },
    medicalArchival_up (state, obj) {
      concat_obj(state.medicalArchival, obj);;
    },
    user_up (state, obj) {
      concat_obj(state.user, obj);;
    },
    doctor_up (state, obj) {
      concat_obj(state.doctor, obj);;
    },
  }
})



