// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import routes from './router'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'

import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

import store from './store/store.js'
import Render from './Render'
import httpinterceptor from './config/httpinterceptor'
import './style/main.less'


Vue.use(AjaxPlugin)
Vue.use(VueRouter)
Vue.use(WechatPlugin)

const router = new VueRouter({
routes
})

FastClick.attach(document.body);
Vue.config.productionTip = false;
Vue.directive('title', { // 注册设置页面标签的自定义指令
inserted: function (el, binding) {
document.title = el.dataset.title
}
})
var app = new Vue({
created(){
httpinterceptor.init(this);
},
router,
store,
render: h => h(Render)
}).$mount('#app-box')



